<template>
    <el-container>
      <el-header class="header" style="background: #00ffff">
          <div class="heading_left">
            <i class="el-icon-s-platform"></i>
          </div>
          <div class="heading_right">
            <div class="change-icon" style="font-size:30px;">
            <i class="el-icon-user-solid"></i>
            </div>
            <el-dropdown @command="handleCommand"><span class="el-dropdown-link">{{ nickName }}<i
              class="el-icon-arrow-down el-icon--right"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="goToHelloWorld"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>

      <el-container class="background">
      <el-aside width="250px">
        <ScientistMenu></ScientistMenu>
      </el-aside>
        <el-main style="padding-left: 5%; padding-right: 10%" class="main">
          <el-row>
            <el-col :span="24">
              <el-row>
                <el-col :span="18">
                  <el-input
                    placeholder=""
                    prefix-icon="el-icon-search" v-model="inputSearch"
                    style="margin-bottom: 3%"></el-input>
                </el-col>
                <el-col :span="6">
                  <el-button
                    type="primary"
                    icon="el-icon-search"
                    style="float: left; margin-right: 10px;margin-left: 20px"
                    @click="searchDiscuss(inputSearch)"
                    circle>
                  </el-button>
                  <el-button
                    type="primary"
                    icon="el-icon-plus"
                    @click="buildThemeVisible = true"></el-button>
                  <el-dialog title="" :visible.sync = "buildThemeVisible" >
                    <el-form ref="form" :model="form" label-width="120px">
                      <el-form-item label="">
                        <el-input v-model="form.name"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                        <el-input v-model="form.introduction"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                        <el-select v-model="form.type" placeholder="">
                          <el-option label="" value=""></el-option>
                          <el-option label="" value=""></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="">
                        <el-select v-model="form.scenes" placeholder="">
                          <el-option label="" value=""></el-option>
                          <el-option label="" value=""></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="">
                        <el-select v-model="form.data" placeholder="">
                          <el-option label="ANDI dataset" value="ANDI dataset"></el-option>
                          <el-option label="FFAIR dataset" value="FFAIR dataset"></el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="">
                        <el-upload
                          class="upload-demo"
                          action="https://jsonplaceholder.typicode.com/posts/"
                          :on-preview="handlePreview"
                          :on-remove="handleRemove"
                          :before-remove="beforeRemove"
                          multiple
                          :limit="3"
                          :on-exceed="handleExceed"
                          :file-list="fileList">
                          <el-button size="small" type="primary"></el-button>
                          <div slot="tip" class="el-upload__tip">zip/rar50GB</div>
                        </el-upload>
                      </el-form-item>
                      <el-form-item>
                        <el-button type="primary" @click="uploadModel"></el-button>
                        <el-button @click="buildThemeVisible=false"></el-button>
                      </el-form-item>
                    </el-form>
                  </el-dialog>
                  <el-dialog title="" :visible.sync = "showDataContent" >
                    <el-form ref="form" :model="detail" label-width="120px">
                      <el-form-item label=":">
                        {{detail.name}}
                      </el-form-item>
                      <el-form-item label=":">
                        {{detail.time}}
                      </el-form-item>
                      <el-form-item label="">
                        {{detail.introduction}}
                      </el-form-item>
                      <el-form-item label="">
                        {{detail.scenes}}
                      </el-form-item>
                      <el-form-item label="">
                        {{detail.data}}
                      </el-form-item>
                      <el-form-item>
                        <el-button type="primary" @click="showDataContent=false"></el-button>
                      </el-form-item>
                    </el-form>
                  </el-dialog>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
          <el-divider></el-divider>
          <el-table
            :data="models"
            :header-cell-style="{'text-align':'center'}"
            :cell-style="{'text-align':'center'}"
            style="width: 100%">
            <el-table-column
              label=""
              prop="name"
              width="180">
            </el-table-column>
            <el-table-column
              label="&"
              width="180">
              <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.scenes }}</el-tag>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                 <el-tag size="medium">{{ scope.row.type }}</el-tag>
              </template>
            </el-table-column>
             <el-table-column label="">
              <template slot-scope="scope">
                <i class="el-icon-time"></i>
                <span >{{ scope.row.time }}</span>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="showDetail(scope.$index)"></el-button>
                <el-button
                  v-if="userName===scope.row.ownerID"
                  size="mini"
                  type="danger"
                  @click="confirmDialogDelete(scope.$index)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-dialog
            :visible.sync="deleteDialogVisible"
            title=""
            width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
              <el-button type="primary" @click="confirmDelete"></el-button>
              <el-button @click="deleteDialogVisible=false"></el-button>
            </span>
          </el-dialog>
        </el-main>
      </el-container>
    </el-container>
</template>

<script>
import ScientistMenu from '../ScientistMenu'
export default {
  name: 'ScientistModel',
  components: {ScientistMenu},
  data: function () {
    return {
      loading: true,
      userName: '',
      nickName: '',
      inputSearch: '',
      input: {
        title: '',
        content: ''
      },
      models: [{
        id: '1',
        name: 'Multi-modality Fusion Model',
        type: '',
        ownerID: '1',
        ownerName: '',
        time: '2021-12-26 01:01:01',
        scenes: '',
        data: 'ANDI dataset',
        introduction: 'LSTM'
      }, {
        id: '2',
        name: 'S-GNN',
        type: '',
        ownerID: '2',
        ownerName: '',
        time: '2022-7-13 01:02:01',
        scenes: '',
        data: 'ANDI dataset',
        introduction: 'SGNN'
      }],
      form: {
        name: '',
        introduction: '',
        scenes: '',
        type: '',
        data: ''
      },
      detail: {
        id: '1',
        name: 'data1',
        ownerID: '2',
        ownerName: 'sa',
        time: '2001-2-26 01:01:01',
        size: '32MB',
        scenes: '',
        introduction: 'FBRNDataBases is used to Backward'
      },
      buildThemeVisible: false,
      showDataContent: false,
      deleteDialogVisible: false,
      time: ''
    }
  },
  mounted: function () {
    this.userName = this.cookie.getCookie('userName')
    this.nickName = this.cookie.getCookie('nickName')
    this.getModelList()
  },
  methods: {
    uploadModel () {
      this.buildThemeVisible = false
      let that = this
      that.$http.request({
        url: that.$url + 'uploadModel/',
        method: 'get',
        params: {
          ownerID: that.userName,
          ownerName: that.nickName,
          name: that.form.name,
          introduction: that.form.introduction,
          scene: that.form.scenes,
          type: that.form.type,
          data: that.form.data
        }
      }).then(function (response) {
        that.getModelList()
      })
    },
    confirmDialogDelete (row) {
      this.deleteDialogVisible = true
      this.curIndex = row
    },
    confirmDelete () {
      this.deleteDialogVisible = false
      let that = this
      console.log(that.curIndex)
      that.$http.request({
        url: that.$url + 'DeleteModel/',
        method: 'get',
        params: {
          modelID: that.models[that.curIndex].id
        }
      }).then(function (response) {
        that.getModelList()
      })
    },
    getModelList: function () {
      let that = this
      that.$http.request({
        url: that.$url + 'getModelList/',
        method: 'get'
      }).then(function (response) {
        that.models = response.data
      })
    },
    showDetail (row) {
    // 
      this.detail = this.models[row]
      this.showDataContent = true
    },
    showAddHomeworkDialog () {
      this.addHomeworkDialogVisible = true
    },
    clearAddHomeworkForm () {
      this.$refs.addHomeworkForm.resetFields()
    },
    goToHelloWorld: function () {
      this.cookie.clearCookie('userName')
      this.cookie.clearCookie('nickName')
      this.$router.replace('/')
    },
    handleCommand: function (command) {
      this.goToHelloWorld()
    }
  }
}
</script>

<style scoped>
@import "../../../assets/css/back.css";
</style>
<style scoped>

.todo-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}
.todo-input {
  width: 200px;
  margin-bottom: 10px;
}

.todo-select {
  width: 200px;
  margin-bottom: 10px;
}

/*  */
.todo-datepicker {
  width: 200px;
  margin-bottom: 10px;
}
.todo-button {
  margin-bottom: 10px;
}
.todo-table {
  margin-top: 10px;
  width: 400px;
}
.todo-table th {
  background-color: #f5f7fa;
  color: #909399;
  font-weight: bold;
  text-align: left;
}
.todo-table tr:nth-child(odd) {
  background-color: #f9fafc;
}
.custom-table {
  /*  */
}

.custom-table::before {
  content: "";
}
</style>



