<template>
    <el-container>
      <el-header class="header" style="background: #00ffff">
          <div class="heading_left">
            <i class="el-icon-s-platform"></i>
          </div>
          <div class="heading_right">
            <div class="change-icon" style="font-size:30px;">
            <i class="el-icon-user-solid"></i>
            </div>
            <el-dropdown @command="handleCommand"><span class="el-dropdown-link">{{ nickName }}<i
              class="el-icon-arrow-down el-icon--right"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="goToHelloWorld"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
      <el-container class="background">
      <el-aside width="250px">
        <PublicMenu></PublicMenu>
      </el-aside>
        <el-main style="padding-left: 5%; padding-right: 10%" class="main">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span style="font-weight: bold"></span>
          <!--    <el-button style="float: right; padding: 3px 0" type="text"></el-button>-->
            </div>
<!--            <div v-for="o in 4" :key="o" class="text item">-->
<!--              {{'' + o }}-->
<!--            </div>-->
            <div class="text item" v-if="show" v-html="scheme"></div>
            <div class="text item" v-else></div>
          </el-card>
        </el-main>
</el-container>
    </el-container>

</template>

<style module>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>

<script>
import PublicMenu from '../PublicMenu'
export default {
  name: 'PublicRecommendation',
  components: {PublicMenu},
  data: function () {
    return {
      circleUrl: '',
      userName: '',
      nickName: '',
      show: true,
      scheme: '\n' +
        '\n' +
        'Cholinesterase Inhibitors \n' +
        'NMDA \n' +
        '\n' +
        ' \n' +
        ' \n' +
        ' \n' +
        '\n' +
        ' \n' +
        ' \n' +
        ' '
    }
  },
  mounted: function () {
    this.userName = this.cookie.getCookie('userName')
    this.nickName = this.cookie.getCookie('nickName')
    this.getPublicRecommendation()
  },
  methods: {
    getPublicRecommendation: function () {
      let that = this
      that.$http.request({
        url: that.$url + 'getPublicRecommendation/',
        method: 'get',
        params: {
          userName: that.userName
        }
      }).then(function (response) {
        that.show = response.data.state
        that.scheme = response.data.scheme
      })
    },
    goToHelloWorld: function () {
      this.cookie.clearCookie('userName')
      this.cookie.clearCookie('nickName')
      this.$router.replace('/')
    },
    handleCommand: function (command) {
      this.goToHelloWorld()
    }
  }
}
</script>

<style scoped>
@import "../../../assets/css/back.css";
</style>



